import React from "react";
import { Helmet } from "react-helmet";

import Layout from "../Layout";
import "./Home.scss";

import WorldSlider from "../../components/WorldSilder";

import mainGroot from "../../assets/images/main-groot.png";
import textLogo from "../../assets/images/text-logo.png";
import nftImg from "../../assets/images/nft-img.png";
import mobileNftImg from "../../assets/images/mobile-nft-img.png";
import roadmap1 from "../../assets/images/roadmap1.png";
import roadmap2 from "../../assets/images/roadmap2.png";
import roadmap3 from "../../assets/images/roadmap3.png";
import roadmap4 from "../../assets/images/roadmap4.png";
import roadmap5 from "../../assets/images/roadmap5.png";
import roadmap6 from "../../assets/images/roadmap6.png";
import roadmap7 from "../../assets/images/roadmap7.png";
import roadmap8 from "../../assets/images/roadmap8.png";
import roadmap9 from "../../assets/images/roadmap9.png";
import roadmap10 from "../../assets/images/roadmap10.png";
import RoadmapItem from "../../components/RoadmapItem";
import { Redirect } from "react-router";
import { showAlertModal } from "../../state/modal";
import { useDispatch } from "react-redux";
import Alert from "../../components/Alert";
import { Box, Divider } from "@mui/material";

const Home = () => {
  const dispatch = useDispatch();

  if (sessionStorage.__spa_path) {
    const to = sessionStorage.__spa_path;
    sessionStorage.removeItem("__spa_path");
    return <Redirect to={to} />;
  }

  return (
    <Layout>
      <Alert />
      <section className="home-view">
        <Helmet title="Home" />
        <section className="mint-container" id="back-to-top-anchor">
          <img src={mainGroot} alt="main-groot" />
          <div className="content">
            <img src={textLogo} alt="text-logo" />
            <h6>GENERATIVE NFT</h6>
            <p>
                 
              <br />
              2,000 NFT !
            </p>
            <div className="progress-container">
              <p>SOLD OUT!</p>
              <div className="bar-container">
                <div className="bar" style={{ width: "100%" }} />
              </div>
              <button
                className="minting-router-button-diable"
                onClick={() => {
                  dispatch(showAlertModal());
                }}
              >
                MINTING
              </button>
            </div>
          </div>
        </section>
        <Box sx={{ padding: "0px 12px" }}>
          <Divider
            sx={{
              borderBottom: "2px #000 solid",
              width: "100%",
              padding: "0px 20px",
            }}
          />
        </Box>
        <section id="gallery" className="gallery-container">
          <h6>Gallery</h6>
          <p className="pc-content">
             2,000   <b>NFT  !</b>
            <br />
            <b>   </b> , <b> </b> 
            !
          </p>
          <p className="mobile-content">
             2,000   NFT  !   
            ,    !
          </p>
          <img className="nft-pc" src={nftImg} alt="main-groot" />
          <img className="nft-mobile" src={mobileNftImg} alt="main-groot" />
        </section>
        <section id="worldview" className="worldview-container">
          <h6>Worldview</h6>
          <p className="pc-content">
                 {" "}
            <b> </b><br />
            <b> </b>   ?
          </p>
          <p className="mobile-content">
            <b></b>     {" "}
            <b> </b> <b> </b>   ?
          </p>
          <WorldSlider />
        </section>
        <section id="roadmap" className="roadmap-container">
          <h6>Roadmap</h6>
          <p>
            <b> NFT </b>  <b> </b> !
          </p>
          <div className="grid-container-pc">
            <RoadmapItem
              image={roadmap1}
              percent={10}
              description={
                "DSC  \n  \nNFT &   "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap2}
              percent={20}
              description={
                "2022.7.21~24\n     "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap3}
              percent={30}
              description={
                " NFT 1  \n     400ml     "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap4}
              percent={40}
              description={
                " NFT      5%  "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap5}
              percent={50}
              description={
                "  \n \n  "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap6}
              percent={60}
              description={
                " NFT \n7    \n NFT 1 100% "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap7}
              percent={70}
              description={
                "  \n NFT \n ."
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap8}
              percent={80}
              description={
                "  \n  \n   !"
              }
              isComplete
            />
            <div className="empty" />
            <RoadmapItem
              image={roadmap9}
              percent={90}
              description={
                "2~5 NFT    \n \n  !"
              }
              isComplete={false}
            />
            <RoadmapItem
              image={roadmap10}
              percent={100}
              description={
                "  ! \nDSC   \n   \n"
              }
              isComplete={false}
            />
          </div>
          <div className="grid-container-mobile">
            <RoadmapItem
              image={roadmap1}
              percent={10}
              description={
                "DSC      NFT &   "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap2}
              percent={20}
              description={
                "2022.7.21~24\n     "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap3}
              percent={30}
              description={
                " NFT 1       400ml     "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap4}
              percent={40}
              description={
                " NFT      5%  "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap5}
              percent={50}
              description={
                "      "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap6}
              percent={60}
              description={
                " NFT 7     NFT 1 100% "
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap7}
              percent={70}
              description={
                "  \n NFT  ."
              }
              isComplete
            />
            <RoadmapItem
              image={roadmap8}
              percent={80}
              description={
                "       !"
              }
              isComplete
            />
            <div className="empty" />
            <RoadmapItem
              image={roadmap9}
              percent={90}
              description={
                "2~5 NFT       !"
              }
              isComplete={false}
            />
            <RoadmapItem
              image={roadmap10}
              percent={100}
              description={
                "  ! \nDSC      "
              }
              isComplete={false}
            />
          </div>
        </section>
      </section>
    </Layout>
  );
};

export default Home;
